import { useCallback } from 'react';
import { AppConfig } from '../types';

export const usePagePath = (configuration: AppConfig) => {
  const getPagePath = useCallback(
    (pageId?: string) => {
      if (!pageId) {
        return '/';
      }

      const currentPage = configuration.pages.find(
        (page) => page.id === pageId,
      );

      let path;
      switch (currentPage?.type) {
        case 'collectionPage':
          path = currentPage.collectionPage?.route.path;
          break;
        case 'entityPage':
          path = `${currentPage.entityPage?.route.path}?`;
          break;
        default:
          path = undefined;
      }

      if (process.env.NODE_ENV === 'test') {
        // in test mode we sometime override pages so we need to return the path or '/'
        return path || '/';
      }

      return path;
    },
    [configuration],
  );

  return { getPagePath };
};
